import {
	$getStorage,
	$setStorage,
} from "../utils/auth.js";

import Vue from 'vue'

import {
	BASE_URL
} from "../config/index.js";


// platformType  1wechat    2ali   3byte_dance 
// clientType   1miniapp    2app   3h5
let platformType = ''
let platformSubType = uni.getSystemInfoSync().appName || ''
let clientType = ''
let osType = uni.getSystemInfoSync().platform || ''


platformType = 'wechat'
clientType = 'miniapp'


// console.log('systeminfo====>', uni.getSystemInfoSync())


























// deviceMark
//   platformType_platformSubType_clientType_osType
const deviceMark = platformType + '-' + platformSubType + '-' + clientType + '-' + osType

var lastUrl = null
var lastUrlTime = Date.now()
var lastMethod = null

var lastToLoginPageTime = 0 

module.exports = function request(
	url,
	method,
	data,
	header = {}
) {
	// 
	if (typeof url == 'object') {
		method = url.method
		data = url.data
		header = url.header
		url = url.url
	}
	
	// 0.8POST
	if (method === 'POST' && lastUrl === url && lastMethod === method && (Date.now()-lastUrlTime < 500)) {
		// uni.showToast({
		// 	title: '~',
		// 	icon: 'none'
		// })
		uni.hideLoading()
		console.log('POST')
		return false
	}
	
	// 
	lastUrl = url
	lastUrlTime = Date.now()
	lastMethod = method

	method = (method || 'GET').toUpperCase();
	header = {
		...header,
		Authorization: $getStorage('token') || "",
		'Client-Type': deviceMark,
		'Client-Name': 'default',
	}

















	
	
	url = BASE_URL + url;
	
	let handleFun = (res, resolve, reject) => {
		switch (res.statusCode) {
			case 200:
				resolve(res.data)
				break;
			case 401:
			    reject(res)
				uni.hideLoading()
				$setStorage('token', '');
			
				if (lastToLoginPageTime) {
					let time = (new Date()).getTime() - lastToLoginPageTime
					if (time < 1000) {
						// console.log('')
						break
					}
				}
				
				lastToLoginPageTime = (new Date()).getTime()
				
				// reject(res);














				





				

				uni.navigateTo({
					url: '/pages/login/index'
				})

				
				break;
			case 409:
				reject(res);
				uni.hideLoading()
				if (res.data.code == 40012) {
					// 
					uni.showModal({
						// title: '',
						title: '',
						confirmText: '',
						success: (res) => {
							if (res.confirm) {
								uni.navigateTo({
									url: '/pages/myProfile/index'
								})
							}
						}
					})
				}
				else if (res.data.code == 40021) {
					// console.log('code error ====>')
					uni.showModal({
						title: '',
						content: '',
						confirmText: '',
						success: r => {
							if (r.confirm) {
								uni.navigateTo({
									url: '/pages/myScore/buy'
								})
							}
						}
					})
				}
				else {
					uni.showModal({
						// title: '',
						title: res.data.message || '',
						showCancel: false
					})
				}
				break;
			case 500:
				reject(res);
				uni.showModal({
					title: '',
					content: '~',
					showCancel: false
				})
				uni.hideLoading()
				break;
			default:
				uni.hideLoading()
				reject(res)
				break;
		}
	}
	
	return new Promise((resolve, reject) => {
		uni.request({
			url: url,
			method: method || 'GET',
			data: data,
			header: header,
			success: res => {
			},
			error: err => {
				uni.hideLoading()
				reject(err);
				uni.showModal({
					title: '',
					content: '',
					showCancel: false
				});
			},
			complete: res => {
				// 
				handleFun(res, resolve, reject)
			}
		});
	});

}
